module.exports = {
	svgToUrl(key, color) {
		var url = svgData[key]
		var encoded = url
			.replace(/<!--(.*)-->/g, "")
			.replace(/[\r\n]/g, " ")
			.replace(/"/g, `'`)
			.replace(/%/g, "%25")
			.replace(/&/g, "%26")
			.replace(/#/g, "%23")
			.replace(/{/g, "%7B")
			.replace(/}/g, "%7D")
			.replace(/</g, "%3C")
			.replace(/>/g, "%3E")
		let res = '"' + `data:image/svg+xml,${encoded}` + '"';
		if (color) {
			res = res.replaceAll("%23325DFF", color.replace("#", "%23"))
		}
		return res
	},
}